.center {
    position: absolute;
    top: 47%;
    /* background: #4990E2; */
    color: white;
    text-align: center;
    z-index: 1;
    /* background: rgba(99, 189, 241, 0.5); */
}

.center>h1 {
    color: white;
    letter-spacing: .8px;
    font-size: 60px;
    padding: 5px 0px 15px;
}

.center>h1>#ng {
    color: white;
    font-size: 60px;
    color: white;
    border-radius: 50%;
    height: 110px;
    line-height: 75px;
    width: 110px;
    background: #017dc7;
    font-weight: bolder;
    display: inline-block;
}

.center>h1>span {
    padding: 15px 0px 5px;
    display: block;
    font-size: 13px;
    color: white;
    font-weight: 100;
    letter-spacing: .8px;
}

.center>h3 {
    font-weight: 500;
    font-size: 22px;
    letter-spacing: 1.5px;
    line-height: 40px;
    padding: 20px 0px;
}

.getATutor {
    color: white;
    background: rgb(45, 194, 157);
    padding: 25px 25px;
    border-radius: 60px;
    font-size: 16px;
    display: inline-block;
    letter-spacing: 1.5px;
    font-weight: bolder;
}

@media(max-width: 767px) {
    .center {
        left: 0;
        right: 0;
        padding: 100px 10px;
        -webkit-transform: translateY(-50%) scale(.8, .8);
            -ms-transform: translateY(-50%) scale(.8, .8);
                transform: translateY(-50%) scale(.8, .8);
    }
}

@media(min-width: 768px) {
    .center {
        left: 25%;
        right: 25%;
        padding: 100px 50px;
        -webkit-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
                transform: translateY(-50%);
    }
}


