.slider {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
}

.slider {
    z-index: 1;
}

.slide {
    height: 100%;
    width: 100%;
    position: relative;
    float: left;
}

.slide::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    right: 0;
    background: #444;
    opacity: .775;
}

.bottom {
    height: 92.5px;
    position: absolute;
    bottom: 0px;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    z-index: 100000;
}

button.dot {
    background: #f7f7f7;
    height: 15px !important;
    width: 15px !important;
    border-radius: 50%;
    border: 0px;
    margin: 35px 10px;
    -webkit-transition: 400ms;
    -o-transition: 400ms;
    transition: 400ms;
}

button.active {
    -webkit-transform: scale(1.1, 1.1);
        -ms-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    background: #2a9ae6;
}


