.social-links ul li a i {
    line-height: 55px;
    text-align: center;
    margin: auto;
    display: block;
    -webkit-transition: 500ms;
    -o-transition: 500ms;
    transition: 500ms;
}

.sidebar {
    -webkit-transition: 500ms;
    -o-transition: 500ms;
    transition: 500ms;
    position: relative;
    left: -8.33%;
    border: 0;
    z-index: 100;
}

@media(max-width: 767px) {
    .pullOut button {
        display: none;
    }
}

.pullOut button {
    border: 0px;
    font-size: 20px;
    position: absolute;
    top: 50vh;
    background: lightskyblue;
    color: white;
    height: 90px;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 0%;
    -webkit-transition: 500ms;
    -o-transition: 500ms;
    transition: 500ms;
    z-index: 100;
    width: 90px;
}


